import React from 'react';
import withStyles from 'isomorphic-style-loader/lib/withStyles';
import PropTypes from 'prop-types';
import classNames from 'classnames/bind';

import utils from '../utils';
import Intersection from './MoveIntersection';
// eslint-disable-next-line css-modules/no-unused-class
import s from '../BoardLayer.css';

const cx = classNames.bind(s);

class MoveLayer extends React.Component {
  static propTypes = {
    boardsize: PropTypes.number.isRequired,
    moves: PropTypes.arrayOf(PropTypes.arrayOf(PropTypes.number)).isRequired,
    board: PropTypes.arrayOf(PropTypes.arrayOf(PropTypes.number)).isRequired,
  };

  constructor(props) {
    super(props);
    this.intersection = this.intersection.bind(this);
  }

  intersection(i, j) {
    return (
      <Intersection
        key={`${i},${j}`}
        row={i}
        col={j}
        stone={this.props.board[i][j]}
        value={this.props.moves[i][j]}
        boardsize={this.props.boardsize}
      />
    );
  }

  render() {
    return utils.render(this.props.boardsize, cx, this.intersection);
  }
}

export default withStyles(s)(MoveLayer);



